  methods: {
    //初始化中国地图
    initEchartMap() {
      let mapDiv = document.getElementById("china_map");
      let myChart = this.$echarts.init(mapDiv);
      myChart.setOption(this.options);

      /* ECharts自适应屏幕尺寸 */
      window.addEventListener("resize", () => {
        myChart.resize();
      });
    },
    //修改echart配制
    setEchartOption() {
      this.getCarStatus(); // 获取全部车辆
      setTimeout(() => {
        this.options.series[0]["data"] = this.dataList;
      }, 200);
    },

    /**
     * @Interface 接口
     * @description: 获取不同省份的数据
     * */
    getCarStatus() {
      getAreaCount().then((res) => {
        if (res.data.code === 1) {
          this.dataList.forEach((item, index) => {
            switch (item.name) {
              case "甘肃":
                item.value = res.data.data.gansu;
                break;
              case "广西":
                item.value = res.data.data.guangxi;
                break;
              case "江苏":
                item.value = res.data.data.jiangsu;
                break;
              case "内蒙古":
                item.value = res.data.data.neimenggu;
                break;
              case "青海":
                item.value = res.data.data.qinghai;
                break;
              case "浙江":
                item.value = res.data.data.zhejiang;
                break;
              default:
                item.value = 0;
                break;
            }
          });
        }
      });
    },

    /**
     * @Event 方法
     * @description: 给子组件用的方法
     * */
    back() {
      this.show1 = false;
      this.setEchartOption();
      setTimeout(() => {
        this.initEchartMap();
      }, 500);
    },
